/**
 * Site header
 */
.site-header {
  // border-top: 2px solid $grey-color-dark;
  min-height: 80px;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  float: left;
  display: block;
  width: 50px;
  height: 50px;
  background-image: url(/assets/logo.png);
  background-size: 50px 50px;
  background-repeat: no-repeat;
  background-position: 0 0;

  @media
  only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (   min--moz-device-pixel-ratio: 2),
  only screen and (     -o-min-device-pixel-ratio: 2/1),
  only screen and (        min-device-pixel-ratio: 2),
  only screen and (                min-resolution: 192dpi),
  only screen and (                min-resolution: 2dppx) {
    background-image: url(/assets/logo@3x.png);
  }
  @media (prefers-color-scheme: dark) {
    filter: invert(100%);
  }

  text-indent : -9999px;
  margin-top: 20px;
}

.site-nav {
  float: right;
  margin-top: 20px;
  line-height: 50px;
  z-index: 10;

  .menu-icon {
    display: none;
  }

  .page-link, .page-link-ext {
    color: $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @media (prefers-color-scheme: dark) {
    .page-link, .page-link-ext {
      color: $text-color-dark;
    }
  }

  @include media-query($on-palm) {
    position: absolute;
    top: 9px;
    right: calc($spacing-unit / 2);
    background-color: $background-color;
    border: 1px solid $grey-color-light;
    border-radius: 5px;
    text-align: right;

    .menu-icon {
      display: block;
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;

      > svg path {
        fill: $text-color-gray-dark;
      }
    }

    .trigger {
      clear: both;
      display: none;
    }

    &:hover .trigger {
      display: block;
      padding-left: 0.5em;
      padding-right: 0.5em;
    }

    .page-link, .page-link-ext {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }
      margin-left: 20px;
    }

    @media (prefers-color-scheme: dark) {
      background-color: $background-color-dark;
    }
  }
}

#category-picker-controller {
  padding-bottom: 1px;

  #cp-icon {
    position: relative;
    font-size: 1.2em;
    a {
      position: absolute;
      right: 0.2em;
      top:0.2em;
      color: $filter-color;
      &:hover {
        color: $text-color-hover;
      }
    }
  }

  #category-picker {
    border-top:1px solid black;
    border-bottom: 1px solid black;
    @media (prefers-color-scheme: dark) {
        border-color: $grey-color-picker;
    }
    overflow:auto;
    padding: 1em 0.5em;
    font-size: $base-font-size * 0.8;

    ul {
      list-style-type: none;
    }
    li {
      float: left;
      margin: 0.2em;
      line-height: 1em;
      border: 1px solid black;
      @media (prefers-color-scheme: dark) {
          border-color: $grey-color-picker;
      }
      border-radius: 1em;
      transition: all .3s ease-in-out;
      -webkit-transition: all .3s ease-in-out;
      -moz-transition: all .3s ease-in-out;
      -ms-transition: all .3s ease-in-out;
      -o-transition: all .3s ease-in-out;

      &:hover {
        background-color: $text-color;
        @media (prefers-color-scheme: dark) {
            background-color: $grey-color-picker;
        }
        a {
          color: $text-color-white;
          text-decoration: none;
        }
      }
    }

    .active {
      background-color: $text-color;
      @media (prefers-color-scheme: dark) {
          background-color: $grey-color-picker;
      }
      a {
        color: $text-color-white;
        text-decoration: none;
      }
    }

  }


  ul li a {
    display: block;
    padding: 0.2em 0.4em 0.3em 0.4em;

    transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0 calc($spacing-unit / 2) 0;
}

.footer-heading {
  font-size: 18px;
  margin-bottom: calc($spacing-unit / 2);
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  font-size: 15px;
  color: $grey-color;
  margin-left: calc($spacing-unit / 2) * -1;
  @extend %clearfix;
}

.footer-col {
  float: left;
  padding-left: calc($spacing-unit / 2);
}

.footer-col-1 {
  width: -webkit-calc(40% - (#{$spacing-unit} / 2));
  width:         calc(40% - (#{$spacing-unit} / 2));
}
//
// .footer-col-2 {
//   width: -webkit-calc(20% - (#{$spacing-unit} / 2));
//   width:         calc(20% - (#{$spacing-unit} / 2));
// }

.footer-col-2 {
  width: -webkit-calc(60% - (#{$spacing-unit} / 2));
  width:         calc(60% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
  .footer-col-1 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }

  .footer-col-2 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-content, .page-content-clone {
  padding: $spacing-unit 0;
}

.page-heading {
  font-size: 20px;
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: calc($spacing-unit / 3);

    width: 100%;
    height: 2.75em;
    transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    -moz-transition: all .4s ease-in-out;
    -ms-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;

    > h2 {
      white-space: nowrap;
    }
    @include media-query($on-palm) {
      > h2 {
        white-space: normal;;
      }
      .home-post-meta {
        display: none;
      }
    }
    @include media-query($on-laptop) {
      > h2 {
        white-space: normal;;
      }
    }

  }
  @include media-query($on-palm) {
    > li {
      height: 100%;
    }
  }
  @include media-query($on-laptop) {
    > li {
      height: 100%;
    }
  }

  .hide {
    opacity: 0;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
}

.home {
  margin-left: 1em;
  margin: 1em;
}
.home-post-meta {
  font-size: $small-font-size * 0.7;
  color: $grey-color;
  vertical-align: 0.3em;
  margin-left: -1em;
  opacity: 0;
  // display: none;
}

.post-meta {
  display: block;
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  font-size: 1.1em;
  margin-right: 0.5em;
}



/**
 * Posts
 */
 .post {
   margin-left: 1em;
   margin-right: 1em;
 }
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  font-size: 2.2em;
  letter-spacing: -0.01em;
  line-height: 1.2em;

  @include media-query($on-laptop) {
    font-size: 36px;
  }
}

.post-content {
  margin-bottom: $spacing-unit;
  line-height: 1.75em;

  strong {
    font-weight: 400;
  }

  sup {
    line-height: 1em;
  }

  .highlight {
    line-height: 1.2em;
  }

  a {
    color: $text-color;
    text-decoration: underline;

    // &:visited {
    //   color: $text-color-visited;
    // }

    &:hover {
      color: $text-color-hover;
      text-decoration: none;
    }
  }

  @media (prefers-color-scheme: dark) {
    a {
      color: $text-color-dark;

      &:hover {
        color: $text-color-hover-dark;
      }
    }
  }

  h1 {
    font-size: 1.9em;
    margin-top: 1.7em;
    margin-bottom: 1em;

    @include media-query($on-laptop) {
      font-size: 30px;
    }
  }


  h2 {
    font-size: 1.7em;
    margin-top: 1.5em;
    margin-bottom: 1em;

    @include media-query($on-laptop) {
      font-size: 28px;
    }
  }

  h3 {
    font-size: 1.5em;
    margin-top: 1.2em;

    @include media-query($on-laptop) {
      font-size: 22px;
    }
  }

  h4 {
    font-size: 1.3em;
    margin-top: 1.1em;

    @include media-query($on-laptop) {
      font-size: 18px;
    }
  }

  h5 {
    font-size: 1.1em;

    @include media-query($on-laptop) {
      font-size: 16px;
    }
  }

  code {
    vertical-align: top;
  }

  hr {
    border: 0;
    height: 1px;
    background-image: -webkit-linear-gradient(left, #f0f0f0, #8c8b8b, #f0f0f0);
    background-image: -moz-linear-gradient(left, #f0f0f0, #8c8b8b, #f0f0f0);
    background-image: -ms-linear-gradient(left, #f0f0f0, #8c8b8b, #f0f0f0);
    background-image: -o-linear-gradient(left, #f0f0f0, #8c8b8b, #f0f0f0);
    margin-top: $spacing-unit;
    margin-bottom: $spacing-unit;
  }

  img:not(p a img) {
    max-width: calc(100% + 2em + 30px);
    margin-left: calc(-1em - 15px);
    margin-right: calc(-1em - 15px);
    max-width: -webkit-calc(100% + 2em + 30px);
    margin-left: -webkit-calc(-1em - 15px);
    margin-right: -webkit-calc(-1em - 15px);
  }
}


// toTop link
#toTop {
  text-align: center;
  margin-bottom: 2em;

  a {
    font-size: 1.5em;
  }
}
#loadMore {
  text-align: center;
  margin-bottom: 2em;
  padding-top: 2em;

  a {
    font-size: 1.5em;
    text-decoration: none;
  }
}
// table style
table {
  font-size: $base-font-size * 0.8;
  margin-top: 1em;
  margin-bottom: 1.75em;
  width: 100%;
  vertical-align: middle;
  text-align: center;

  th {
    border-bottom: 1px solid $text-color-gray;
  }

  th, td {
    padding: 0.3em;
  }
}

// footnotes
.footnotes {
  font-size: $base-font-size * 0.8;
  border-top: 1px dotted $text-color-gray;
  padding-top: 1.75em;
  margin-top: 4em;

  p {
    margin-bottom: 0.3em;
    vertical-align: baseline;
  }

  .reversefootnote {
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    font-size: $base-font-size * 1.1;
  }
}

.footnote {
  padding: 0 0.2em;
}

:target {
  animation: highlight 5s ease;
}

@keyframes highlight {
  0% { background-color: rgba(255, 255, 0, 0.3); }
  100% { background-color: transparent; }
}

// @media (prefers-color-scheme: dark) {
//   @keyframes highlight {
//     0% { background-color: #808040; }
//     100% { background-color: transparent; }
//   }
// }

// qrcode
#qrcode {
  img {
    margin: auto;
  }
}

#discussion-btn-group {
  text-align: center;
  padding-top: 4em;
  padding-bottom: 4em;

  .discussion-btn {
    background-color: $grey-color-light;
    color: $text-color;
    border: none;
    padding: 0.6em 2em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin-right: 1em;
    min-width: 10em;

    @include media-query($on-palm) {
      margin-right: 0;
      margin-top: 1em;
    }

    @media (prefers-color-scheme: dark) {
      filter: invert(100%);
    }

    &:hover {
      color: $text-color
    }
  }
}

.categories {
  list-style: none;
  margin: 0px;
  padding: 2em 0 2em 0;
  font-family: $code-font-family;
  font-size: 0.8em;
  font-weight: lighter;

  li {
    float: left;
    padding-right: 0.5em;
    a {
      color: $text-color-gray;

      @media (prefers-color-scheme: dark) {
        filter: invert(100%);
      }
    }
  }
}

.post-nav {
  margin: 1em 0 1em 0;
  display: flex;

  @include media-query($on-palm) {
    display: block;

    .post-nav-next {
      padding-top: 1em;
    }
  }


  div {
    /* flex-grow, flex-shrink, flex-basis */
    flex: 1 1 0;
    #prev-span {
      padding-left: 0.2em;
    }
    #next-span {
      padding-right: 0.2em;
    }
  }

  .post-nav-next {
    text-align: right;
  }
}

.ending {
  opacity: 0.4;
  transition: 0.7s;

  &:hover {
    opacity: 1;
  }

  @include media-query($on-palm) {
    opacity: 1;
  }
}
